.checkbox__div {
  width: 40px;
  height: 20px;
  background-color: #fff;
  border: 1px solid #fd5103;
  border-radius: 40px;
  transition: all .3s;
  position: relative;
}

.checkbox__div:before {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #fd5103;
  border-radius: 50%;
  transition: all .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.checkbox__input {
  display: none;
}

.checkbox__input:checked ~ .checkbox__div {
  background-color: #fd5103;
  border: 1px solid #fff;
}

.checkbox__input:checked ~ .checkbox__div:before {
